<template>
	<div class="deploy-info">
		<div class="wheel-planting">
			<el-carousel indicator-position="outside">
				<el-carousel-item v-for="(item,key) in list" :key="key">
					<img :src="item" alt="" />
				</el-carousel-item>
			</el-carousel>
		</div>
		<div class="deploy-content">
			<div id="deploy" ref="deploy"></div>
			<div style="position: absolute;top: 600px;left: 330px;">
				<span style="width:10px; height:10px; border-radius:50%; display:inline-block; background:#30C9AE"></span>
				<span id="deploys" style="color:#757A97">{{lang.base.deploying}}</span>
			</div>
			<div class="duration">
				<div id="txt">{{lang.base.duration}}:<span>15分钟38秒</span></div>
			</div>
			<div class="deploy-status">
				<el-switch
					class="switch-btn"
				  v-model="cloudUp"
				  :inactive-text="lang.base.cloudPlatform" disabled>
				</el-switch>
				<el-switch
				  class="switch-btn"
				  v-model="bigdataUp"
				  :inactive-text="lang.base.bigData" disabled>
				</el-switch>
				<el-switch
				  class="switch-btn"
				  v-model="mirroringUp"
				  :inactive-text="lang.base.uploadMirroring" disabled>
				</el-switch>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				list: ['/static/image/cloud-bg.png', '/static/image/storage-bg.png', '/static/image/bigdata-bg.png'],
				cloudUp:true,
				bigdataUp:false,
				mirroringUp:false
			}
		},
		methods: {
			getDeployProcess(){
				let self = this
				this.$http({
					url: this.api.get_deploy_process,
					method: 'get'
				}).then(function(res) {
					if (res.data.code===0) {
						self.initChart(res.data.data.percentage)
					}
				}, function(res) {
					
				});
			},
			initChart(percents) {
				let myChart1 = this.$echarts.init(document.getElementById('deploy'));
				let percent = percents
				let option = {
					title: {
						'text': percent + "%",
						subtext: '已完成',
						textAlign: "center",
						"textStyle": {
							"fontWeight": 'normal',
							"color": '#757A97',
							"fontSize": 20
						},
						"subtextStyle": { //副标题的文字的样式
							"fontWeight": 'bold',
							"fontSize": 16,
							"color": '#757A97'
						},
						top: "90",
						"x": '50%',
						"y": '45%',
						textAlign: "center",
					},
					xAxis: {
						show: false, //是否展示x轴
					},
					yAxis: {
						show: false,

					},
					series: [{
						"name": '',
						bottom: 100,
						"type": 'pie',
						"radius": ['50%', '60%'],
						"avoidLabelOverlap": false,
						"startAngle": 225,
						"color": [{
							type: 'linear',
							x: 0,
							y: 0,
							x2: 0.4,
							y2: 1,
							colorStops: [{
								offset: 0,
								color: "#30C9AE" // 0% 处的颜色
							}, {
								offset: 1,
								color: "#30C9AE" // 100% 处的颜色
							}],
							globalCoord: false // 缺省为 false
						}, 'none'],
						"hoverAnimation": false, //是否开启 hover 在扇区上的放大动画效果。
						"legendHoverLink": false, //是否启用图例 hover 时的联动高亮。
						"label": {
							"normal": {
								"show": false,
								"position": 'center'
							},
							"emphasis": {
								"show": false,
								"textStyle": {
									"fontSize": '30',
									"fontWeight": 'bold'
								}
							}
						},
						"labelLine": {
							"normal": {
								"show": false
							}
						},
						"data": [{
							"value": 75,
							"name": '1'
						}, {
							"value": 25,
							"name": '2'
						}]
					}, {
						"name": '',
						"type": 'pie',
						"radius": ['50%', '60%'],
						"avoidLabelOverlap": false,
						"startAngle": 315,
						"color": ['#e7e7e7', "#fff", "transparent"],
						"hoverAnimation": false,
						"legendHoverLink": false,
						"clockwise": false, //饼图的扇区是否是顺时针排布。
						"z": 10,
						"labelLine": {
							"normal": {
								"show": false
							}
						},
						"data": [{
							"value": (100 - percent) * 270 / 360,
							"name": ''
						}, {
							"value": 1,
							"name": ''
						}, {
							"value": 100 - (100 - percent) * 270 / 360,
							"name": ''
						}]
					}]
				}
				myChart1.setOption(option, true);
			}
		},
		mounted() {
			let self = this
			this.getDeployProcess()
//		 this.interval = setInterval(function() {
//             self.getDeployProcess()
//         }, 3000);
		}
	}
</script>

<style scoped="scoped">
	.wheel-planting {
		width: 1500px;
		height: 300px;
	}
	
	#deploy {
		width: 250px;
		height: 250px;
		/*background: pink;*/
	}
	
	.deploy-content {
		margin-top: 115px;
		margin-left: 240px;
	}
	
	.duration {
		position: absolute;
		left: 500px;
		top: 500px;
	}
	
	.duration #txt {
		font-size: 16px;
		color: #666;
	}
	.duration #txt span{
		color: #4398cb;
		font-size: 18px;
		padding-left: 10px;
	}
	.deploy-status{
		position: absolute;
		left: 800px;
		top: 550px;
	}
	.switch-btn{
		margin-right: 10px;
	}
</style>


