<template>
	<div class="confirm-info">
		<h5>{{lang.base.cloud}}</h5>
		<ul class="cloud-info">
			<li>{{lang.base.account}}:{{cloudInfo.username}}</li>
			<li>{{lang.base.password}}:{{cloudInfo.password}}</li>
			<li>{{lang.base.consoleIp}}:{{cloudInfo.consoleIp}}</li>
		</ul>
		<h5>{{lang.base.bigData}}</h5>
		<el-table class="cloud-table" :data="tableData" stripe style="width: 100%">
			<el-table-column prop="nodeName" :label="lang.base.node">
			</el-table-column>
			<el-table-column prop="network_name" :label="lang.base.nic">
			</el-table-column>
			<el-table-column prop="network_ip" :label="lang.base.ipAddress">
			</el-table-column>
			<el-table-column prop="netmask" :label="lang.base.subnetMask">
			</el-table-column>
			<el-table-column prop="cpu" :label="lang.base.cpu">
			</el-table-column>
			<el-table-column prop="mem" :label="lang.base.memory">
			</el-table-column>
			<el-table-column prop="bigdata" :label="lang.base.storage">
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default{
		data:function(){
			return {
				tableData:[],
				cloudInfo:''
			}
		},
		methods:{
			
		},
		mounted(){
			this.tableData = JSON.parse(sessionStorage.getItem('bigdata'))
			this.cloudInfo = JSON.parse(sessionStorage.getItem('cloudInfo'))
		}
	}
</script>

<style scoped="scoped">
	.confirm-info .cloud-info {
		list-style-type: none;
		height: 50px;
		background: #edf0f6;
		margin-top: 20px;
		overflow: hidden;
	}
	
	.confirm-info .cloud-info li {
		width: 15%;
		line-height: 50px;
		font-size: 16px;
		color: #666;
		float: left;
	}
	
	.confirm-info .confirm-info h5 {
		color: #666;
	}
	.confirm-info .cloud-table{
		margin-top: 20px;
	}
</style>


