<template>
	<el-container>
		<!--头部-->
		<headers></headers>
		<!--中间内容-->
		<el-main class="auto-main">
			<!--步骤条-->
			<div class="steps">
				<el-steps :active="stepIndex" simple>
					<el-step><i class="step01" slot="icon"></i></el-step>
					<el-step><i class="step02" slot="icon"></i></el-step>
					<el-step><i class="step03" slot="icon"></i></el-step>
					<el-step><i class="step04" slot="icon"></i></el-step>
					<el-step><i class="step05" slot="icon"></i></el-step>
				</el-steps>
			</div>
			<!--基础配置的内容-->
			<div class="content">
				<!--选择部署方式-->
				<div v-if="stepIndex===0">
					<div class="choice-mode" v-if="showMode">
						<div :class="['mode-box',activeIndex===index?'active':'']" v-for="(item,index) in lang.base.deploymentMode">
							<div class="mode-title">
								{{item.title}}
							</div>
							<div class="mode-desc">
								<p>{{item.desc}}</p>
							</div>
							<div :class="['checkbox',activeIndex===index?'checked':'nocheck']" @click="choseMode(index,item.title)"></div>
						</div>
					</div>
					<!--选择安装源-->
					<div class="installation-source" v-if="showInstallationSource">
						<span>{{lang.base.selcetInstallSource}}</span>
						<el-select v-model="installSource" :placeholder="lang.base.selcetInstallSource" style="width: 300px;">
							<el-option v-for="item in showInstallationSources" :label="item.SerialNumber" :key="item.id" :value="item.id"></el-option>
						</el-select>
						<div>
							<el-checkbox v-model="checked" style="margin-top: 40px;">{{lang.base.joinUs}}</el-checkbox>
						</div>
						<span slot="footer" class="btnstyle">
							<el-button round @click="installPrev">{{lang.common.prevStep}}</el-button>
            				<el-button type="primary" round @click="installNext" :disabled="installSource===''">{{lang.common.nextStep}}</el-button>
						</span>
					</div>
					<!--选择安装全部还是部分升级-->
					<!--<div class="install-upgrading" v-if="showInstallUpgrading">
						<ul>
							<li :class="checkedIndex===1?'active':''" @click="selectBtn(1)"></li>
							<li v-if="isInstalled===true" :class="checkedIndex===2?'active':''" @click="selectBtn(2)"></li>
						</ul>
					</div>-->
					<!--升级部分功能页面-->
					<div v-show="showUpgrade">
						<div class="upgrade-div">
							<div class="left">
								<ul class="upgrad-btn">
									<li :class="selectIndex===1?'active':''" @click="upgradBtn(1)"></li>
									<!--<li :class="selectIndex===2?'active':''" @click="upgradBtn(2)"></li>-->
								</ul>
							</div>
							<div class="right">
								<h5>{{lang.base.upgradeSelect}}</h5>
								<div class="checkbox-div">
									<el-checkbox-group v-model="checkUpdateList">
										<el-checkbox :label="item.key" v-for="(item,key) in deployType==='kvm'?lang.base.kvmupgradList:lang.base.dockerupgradList" style="line-height: 50px;">
											<div class="checkbox-desc">
												<div class="lefts">
													<img :src="item.imgurl" />
												</div>
												<div class="rights">
													<h6>{{item.name}}</h6>
													<span>{{item.desc}}</span>
												</div>
											</div>
										</el-checkbox>
									</el-checkbox-group>
								</div>
							</div>
						</div>
					</div>
				</div>
				<!--筋斗云配置-->
				<div v-if="stepIndex===1">
					<!--kvm的筋斗云配置-->
					<div class="cloudDiv" v-if="deployType==='kvm'&&isShowNetwork===true">
						<el-form ref="form" :model="form" label-width="120px" :rules="cloudrules">
							<el-row>
								<el-col :span="10">
									<el-form-item :label="lang.base.account" prop="username">
										<el-input v-model="form.username" :disabled="disableCheck" :placeholder="lang.base.accountPlaceholder"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="10">
									<el-form-item :label="lang.base.password" prop="password">
										<el-input type="password" v-model="form.password" :disabled="disableCheck" :placeholder="lang.base.passwordPlaceholder"></el-input>
									</el-form-item>
								</el-col>
								<el-button type="primary" round size="small" v-if="!disableCheck" style="margin-left: 10px;margin-top: 4px;" @click="verifyPw">{{lang.base.check}}</el-button>
							</el-row>
							<el-row v-if="errorDialogVisible">
								<el-col :offset="3">
									<span style="color: #F56C6C;">{{lang.base.errorAlert}}</span>
								</el-col>
							</el-row>
							<!--<div>-->
							<div v-if="showNetwork===true">
								<el-row>
									<el-col :span="10">
										<el-form-item :label="lang.base.publicNetwork">
											<el-select v-model="pubnetIndex" placeholder="请选择" style="width: 100%;" @change="changePub">
												<el-option v-for="(item,key) in publicNetwork" :key="key" :value="key" :label="item.name"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
									<el-col :span="10">
										<el-form-item :label="lang.base.selectSubnet">
											<el-select v-model="sunetIndex" placeholder="请选择" style="width: 100%;" @change="changeSubnet">
												<el-option v-for="(item,key) in subnetName" :key="key" :value="key" :label="item.name"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-row>
								<el-row>
									<el-col :span="10">
										<el-form-item :label="lang.base.subnetSegment">
											<el-select v-model="segment" @change="changeSegment" placeholder="请选择" style="width: 100%;">
												<el-option v-for="(item,key) in subnetList" :key="key" :value="key" :label="item.name"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
									<el-col :span="10">
										<el-form-item prop="consoleIp" :label="lang.base.consoleIp" :rules="[{
							required: true,
							message: this.lang.common.required,
							trigger: 'blur'
						}, {
							validator: this.validateGateway,
							message: this.ipValidMessage,
							trigger: 'change'
						},
						{
							validator: this.common.validateIpAddress,
							message: this.lang.base.ipAddressAlert,
							trigger: 'change'
						}
					]">
											<pana-ip-input v-model="form.consoleIp" :disable="disabledArr" :placeholder="placeholderArr" @blur.native.capture="changeConsole"></pana-ip-input>
										</el-form-item>
									</el-col>
									<!--<el-col :span="4">
										<el-button type="primary" style="margin-left: 10px;margin-top: 4px;" round size="small" @click="checkIp">{{lang.base.checkOccupancyGateway}}</el-button>
									</el-col>-->
								</el-row>
								<el-row v-if="ipIsOccupied">
									<el-col :span="10" :offset="12">
										<span style="color: #F56C6C;">{{lang.base.ipIsOccupied}}</span>
									</el-col>
								</el-row>
							</div>
						</el-form>
					</div>
					<!--docker的筋斗云配置-->
					<div v-if="deployType==='docker'||(deployType==='kvm'&&isShowNetwork===false)" class="cloudDiv">
						<el-form ref="form" :model="form" label-width="120px" :rules="cloudrules">
							<el-row>
								<el-col :span="10">
									<el-form-item :label="lang.base.account" prop="username">
										<el-input v-model="form.username" :disabled="disableCheck" :placeholder="lang.base.accountPlaceholder"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="10">
									<el-form-item :label="lang.base.password" prop="password">
										<el-input type="password" v-model="form.password" :disabled="disableCheck" :placeholder="lang.base.passwordPlaceholder"></el-input>
									</el-form-item>
								</el-col>
								<el-button type="primary" round size="small" style="margin-left: 10px;" v-if="!disableCheck" @click="verifyPw">{{lang.base.check}}</el-button>
								<span v-if="disableCheck" style="line-height: 40px;padding-left: 10px;color: #47ab9c;">{{lang.base.connectivitySuccess}}</span>
							</el-row>
							<el-row v-if="errorDialogVisible">
								<el-col :offset="3">
									<span style="color: #F56C6C;">{{lang.base.errorAlert}}</span>
								</el-col>
							</el-row>
						</el-form>
					</div>
					<!--链接正确的提示框-->
					<!--<el-dialog :title="lang.common.alert" :visible.sync="centerDialogVisible" width="30%" center>
						<div class="alert-content">
							<p>{{lang.base.connectSuccess}}</p>
							<span><i class="el-icon-circle-check"></i></span>
						</div>
						<span slot="footer" class="dialog-footer">
						    <el-button @click="showNetworkClick">{{lang.common.cancle}}</el-button>
						    <el-button type="primary" @click="showNetworkClick">{{lang.common.confirm}}</el-button>
						  </span>
					</el-dialog>-->
					<!--密码或者用户名错误-->
					<!--<el-dialog :title="lang.common.alert" :visible.sync="errorDialogVisibles" width="30%" center>
						<div class="alert-content">
							<p>{{lang.base.errorAlert}}</p>
							<span><i class="el-icon-warning"></i></span>
						</div>
						<span slot="footer" class="dialog-footer">
						    <el-button @click="errorClick">{{lang.common.confirm}}</el-button>
						</span>
					</el-dialog>-->
				</div>
				<!--大数据配置-->
				<div v-if="stepIndex===2">
					<div class="bigData">
						<div class="bigData-top" v-if="deployMethod===1">
							<span>{{lang.base.totalStorageSpace}}</span>
							<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="storagePrecent"></el-progress>
							<span class="used">{{lang.base.used}}：{{storageUsed}}/{{storageTotal}}GB</span>
						</div>
						<div class="deploy-method">
							<span>{{lang.base.deployMethod}}</span>
							<el-radio-group class="radio-group" v-model="deployMethod">
								<el-radio :label="1">{{lang.base.ceph}}</el-radio>
								<el-radio :label="2">{{lang.base.bareDisk}}</el-radio>
							</el-radio-group>
						</div>
						<div class="bigdata-content">
							<el-form ref="bigdataForm" :model="bigdataForm" :rules="bigdataRules">
								<el-form-item label="" prop="">
									<el-tabs class="bigdata-tab" v-if="bigdataForm.nodeData.length>0" v-model="activeName" tab-position="left" style="height: 600px;" @tab-click="handleClick">
										<el-tab-pane :label="item.nodeName" :name="item.nodeName" :key="index" v-for="(item, index) in bigdataForm.nodeData" style="height: 510px;">
											<el-row>
												<el-col>
													<span style="padding-left: 60px;">{{item.nodeName}}</span>
												</el-col>
											</el-row>
											<el-row style="margin-top: 30px;">
												<el-col :span="12">
													<el-form-item :prop="'nodeData.'+ index +'.network_name'" :rules="[{required: true,message:lang.common.required, trigger:'blur'}]" :label="lang.base.nic" label-width="120px">
														<el-select v-model="item.network_name" style='width: 100%;' @change="changeNetworkName(index)">
															<el-option v-for='item in nicList' :key="item.network_name" :value="item.network_name" :label="item.network_name"></el-option>
														</el-select>
													</el-form-item>
												</el-col>
												<el-col :span="12">
													<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{
							validator: common.validateIpAddress,
							message: lang.base.ipAddressAlert,
							trigger: 'blur'
						}]" :prop="'nodeData.' + index + '.network_ip'" :label="lang.base.ipAddress" label-width="120px">
														<pana-ip-input v-model="item.network_ip" @blur.native.capture="changeIp(index)"></pana-ip-input>
													</el-form-item>
												</el-col>
											</el-row>
											<el-row style="margin-top: 30px;border-bottom: 1px solid #e0e0e0;padding-bottom: 30px;">
												<el-col :span="12">
													<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{
							validator: common.validateNetMask,
							message: lang.base.netMaskAlert,
							trigger: 'blur'
						}]" :prop="'nodeData.' + index + '.netmask'" :label="lang.base.subnetMask" label-width="120px">
														<pana-ip-input v-model="item.netmask" @blur.native.capture="changeMask(index)"></pana-ip-input>
													</el-form-item>
												</el-col>
											</el-row>
											<el-row>
												<el-col>
													<div style="margin-left: 60px;margin-top: 20px;">{{lang.base.distributionNode}}</div>
												</el-col>
											</el-row>
											<el-row style="margin-top: 30px;">
												<el-col :span="12">
													<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.cpuTotal - item.cpuUsed-1,
                                            message: (item.cpuTotal - item.cpuUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: 'cpu'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.cpuTotal - item.cpuUsed-1})}]" :prop="'nodeData.' + index + '.cpu'" :label="lang.base.cpu" label-width="120px">
														<el-input type="number" v-model.number="item.cpu">
														</el-input>
													</el-form-item>
												</el-col>
												<el-col :span="7">
													<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.cpuUsed/item.cpuTotal)*100)"></el-progress>
												</el-col>
												<el-col :span="5">
													<span>{{lang.base.used}}:{{item.cpuUsed}}/{{item.cpuTotal}}{{lang.base.nucleus}}</span>
												</el-col>
											</el-row>
											<el-row style="margin-top: 30px;">
												<el-col :span="12">
													<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.memTotal - item.memUsed-1,
                                            message: (item.memTotal - item.memUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: '内存'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.memTotal - item.memUsed-1})}]" :prop="'nodeData.' + index + '.mem'" :label="lang.base.memory" label-width="120px">
														<el-input type="number" v-model.number="item.mem"></el-input>
													</el-form-item>
												</el-col>
												<el-col :span="7">
													<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.memUsed/item.memTotal)*100)"></el-progress>
												</el-col>
												<el-col :span="5">
													<span>{{lang.base.used}}:{{item.memUsed}}/{{item.memTotal}}GB</span>
												</el-col>
											</el-row>
											<el-row v-if="deployMethod===1" style="margin-top: 30px;">
												<el-col :span="12">
													<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.diskTotal - item.diskUsed-1,
                                            message: (item.diskTotal - item.diskUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: '存储'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.diskTotal - item.diskUsed-1})}]" :prop="'nodeData.' + index + '.bigdata'" :label="lang.base.storage" label-width="120px">
														<el-input type="number" v-model.number="item.bigdata"></el-input>
													</el-form-item>
												</el-col>
												<el-col :span="7">
													<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.diskUsed/item.diskTotal)*100)"></el-progress>
												</el-col>
												<el-col :span="5">
													<span>{{lang.base.used}}:{{item.diskUsed}}/{{item.diskTotal}}GB</span>
												</el-col>
											</el-row>
											<el-row v-if="deployMethod===2" style="margin-top: 30px;">
												<el-form-item :prop="'nodeData.' + index + '.dev_path'" :label="lang.base.bareDiskStorage" label-width="120px">
													<el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange(index)">全选</el-checkbox>
													<div style="margin: 15px 0;"></div>
													<el-checkbox-group v-model="item.dev_path" @change="handleCheckedCitiesChange(index)">
														<el-checkbox v-for="s in item.diskList" :label="s.byId" :key="s.byId">{{s.name}}</el-checkbox>
													</el-checkbox-group>
												</el-form-item>
											</el-row>
										</el-tab-pane>
									</el-tabs>
								</el-form-item>
							</el-form>
						</div>
					</div>
					<!--<bigdata-setting ref="mychild" :step="stepIndex==2"></bigdata-setting>-->
					<!--大数据是否部署过-->
					<el-dialog :title="lang.common.alert" :visible.sync="deployedData" width="30%" center>
						<div class="alert-content">
							<p>{{lang.base.bigdataDeployed}}</p>
							<span><i class="el-icon-circle-check"></i></span>
						</div>
						<span slot="footer" class="dialog-footer">
		    <el-button @click="cancleDeployedData">{{lang.common.cancle}}</el-button>
		    <el-button type="primary" @click="ensureDeployedData">{{lang.common.confirm}}</el-button>
		  </span>
					</el-dialog>
				</div>
				<!--确认信息-->
				<div v-if="stepIndex===3">
					<div class="confirm-info">
						<h5>{{lang.base.cloud}}</h5>
						<ul class="cloud-info">
							<!--{{cloudInfo.password}}-->
							<li>{{lang.base.account}}: <span>{{cloudInfo.username}}</span></li>
							<li>{{lang.base.password}}: <span>{{ciphertextPassword}}</span></li>
							<li v-if="deployType==='kvm'&&isShowNetwork===true">{{lang.base.consoleIp}}: <span>{{cloudInfo.consoleIp}}</span></li>
						</ul>
						<h5>{{lang.base.bigData}}</h5>
						<el-table class="cloud-table" :data="tableData" stripe style="width: 100%">
							<el-table-column prop="nodeName" :label="lang.base.node">
							</el-table-column>
							<el-table-column prop="network_name" :label="lang.base.nic">
							</el-table-column>
							<el-table-column prop="network_ip" :label="lang.base.ipAddress">
							</el-table-column>
							<el-table-column prop="netmask" :label="lang.base.subnetMask">
							</el-table-column>
							<el-table-column prop="cpu" :label="lang.base.cpu">
							</el-table-column>
							<el-table-column prop="mem" :label="lang.base.memory">
							</el-table-column>
							<el-table-column prop="bigdata" :label="lang.base.storage">
							</el-table-column>
						</el-table>
					</div>
					<!--<confirm-info></confirm-info>-->
				</div>
				<!--开始部署页面-->
				<div v-if="stepIndex===4">
					<div class="deploy-info">
						<div class="wheel-planting">
							<el-carousel indicator-position="outside">
								<el-carousel-item v-for="(item,key) in list" :key="key">
									<img :src="item" alt="" />
								</el-carousel-item>
							</el-carousel>
						</div>
						<div class="deploy-content">
							<div id="deploy" ref="deploy"></div>
							<div style="position: absolute;top: 600px;left: 330px;">
								<span style="width:10px; height:10px; border-radius:50%; display:inline-block; background:#30C9AE"></span>
								<span id="deploys" style="color:#757A97">{{showDeployType}}</span>
							</div>
							<div class="duration">
								<div id="txt">{{lang.base.duration}}:<span id="timetext"></span></div>
							</div>
							<div class="deploy-status">
								<el-switch v-if="(checkedIndex===1&&deployType === 'docker')||checkUpdateList.indexOf('update_docker')>-1" class="switch-btn" v-model="dockerUpgrade" :inactive-text="lang.base.dockerUpgrade" disabled>
								</el-switch>
								<el-switch v-if="checkedIndex===1||checkUpdateList.indexOf('update_storage_image')>-1" class="switch-btn" v-model="mirroringUp" :inactive-text="lang.base.uploadMirroring" disabled>
								</el-switch>
								<el-switch v-if="checkedIndex===1||checkUpdateList.indexOf('update_bigdata')>-1" class="switch-btn" v-model="bigdataUp" :inactive-text="lang.base.bigData" disabled>
								</el-switch>
								<el-switch v-if="(checkedIndex===1&&deployType === 'kvm')||checkUpdateList.indexOf('deploy_kvm')>-1" class="switch-btn" v-model="cloudUp" :inactive-text="lang.base.cloudPlatform" disabled>
								</el-switch>
							</div>
						</div>
					</div>
				</div>
				<!--上一步，下一步，部署的按钮-->
				<span slot="footer" class="dialog-footer footerright">
		            <el-button round v-if="(stepIndex===0&&showInstallUpgrading)||(stepIndex===0&&showUpgrade)||(stepIndex<4&&stepIndex>0)" @click="checkStep(-1)" >{{lang.common.prevStep}}</el-button>
		            <el-button round type="primary" v-if="(stepIndex===0&&showInstallUpgrading)||(stepIndex===0&&showUpgrade)||(stepIndex<3&&stepIndex>0)" @click="checkStep(1)">{{lang.common.nextStep}}</el-button>
		            <el-button round v-if="stepIndex===2" @click="skip">{{lang.common.skip}}</el-button>
		            <el-button round type="primary" v-if="stepIndex===3" @click="startDeploy">{{lang.common.startDeployment}}</el-button>
		            <el-button round type="primary" :disabled="finishBtn" v-if="stepIndex===4" @click="jumpCloud">{{lang.common.finishDeploy}}</el-button>
		        </span>
			</div>
		</el-main>
	</el-container>
</template>

<script>
	import headers from '@/components/header' //头部内容
	import kvmcloud from './kvmcloud' //kvm的筋斗云配置
	import dockercloud from './dockercloud' //docker的筋斗云配置
	import bigdataSetting from './bigdataSetting' //大数据配置界面
	import confirmInfo from './confirmInfo' //确认信息页面
	import deploy from './deploy' //开始部署页面
	import upgrade from './upgrade' //升级页面
	import ipInput from '@/components/ipInput'
	export default {
		components: {
			"headers": headers,
			'kvmcloud': kvmcloud,
			'dockercloud': dockercloud,
			'bigdata-setting': bigdataSetting,
			'confirm-info': confirmInfo,
			'deploy': deploy,
			'upgrade': upgrade,
			'pana-ip-input': ipInput
		},
		data: function() {
			return {
				showInstallationSource: false,
				showMode: true,
				showInstallUpgrading: false,
				installSource: '',
				showInstallationSources: [{
						value: '1',
						label: 'u1'
					},
					{
						value: '2',
						label: 'u2'
					}
				],
				checked: false,
				stepIndex: 0,
				activeIndex: '',
				checkedIndex: '',
				deployType: '',
				isInstalled: false,
				showUpgrade: false,
				//				kvm筋斗云配置
				form: {
					username: '',
					password: '',
					consoleIp: ''
				},
				cloudrules: {
					username: [{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}],
					//					consoleIp: [{
					//							required: true,
					//							message: this.lang.common.required,
					//							trigger: 'blur'
					//						}, {
					//							validator: this.validateGateway,
					//							message: this.ipValidMessage,
					//							trigger: 'change'
					//						},
					//						{
					//							validator: this.common.validateIpAddress,
					//							message: this.lang.base.ipAddressAlert,
					//							trigger: 'change'
					//						}
					//					]
				},
				showNetwork: false,
				centerDialogVisible: false,
				errorDialogVisible: false,
				publicNetwork: [],
				subnetName: [],
				subnetList: [],
				pubnetIndex: 0,
				sunetIndex: 0,
				networkId: '',
				pubnetworkId: '',
				segment: '',
				disabledArr: [],
				ipRange: {},
				placeholderArr: [],
				checkBtnDisable: false,
				checkConsoleIp: false,
				//				大数据页面的数据
				deployedData: false,
				alreadyExist: [],
				deployMethod: 1,
				nodesList: [],
				checkAll: false,
				checkedList: [],
				diskList: [],
				diskIdList: [],
				isIndeterminate: true,
				nicList: [],
				selectNic: '',
				ip: '',
				cpu: '',
				disk: '',
				mem: '',
				activeName: '',
				storageTotal: 0,
				storageUsed: 0,
				storagePrecent: 0,
				cpuPrecent: 0,
				memPrecent: 0,
				diskPrecent: 0,
				bigdataForm: {
					nodeData: []
				},
				bigdataRules: {},
				//				确认信息页面
				tableData: [],
				cloudInfo: '',
				//				部署页面
				list: ['/static/image/cloud-bg.png', '/static/image/storage-bg.png', '/static/image/bigdata-bg.png'],
				cloudUp: false,
				bigdataUp: false,
				mirroringUp: false,
				diachronicTime: '',
				//				部分升级的页面
				checkUpdateList: [],
				selectIndex: 1,
				isShowNetwork: true,
				showDeployType: '',
				finishBtn: true,
				jumpIp: '',
				dockerUpgrade: false,
				ciphertextPassword: '',
				disableCheck: false,
				ipValidMessage: '',
				ipIsOccupied: false
			}
		},
		methods: {
			//          增加步骤条
			checkStep(num) {
				let self = this,
					requiredNodeData = [];
				if(num > 0) {
					if(self.stepIndex === 2) {
						this.bigdataForm.nodeData.forEach(function(item) {
							if(item.cpu !== '') {
								requiredNodeData.push(item)
							}
						})
						self.$refs.bigdataForm.validate((valid) => {
							if(valid === false && requiredNodeData.length >= 3) {
								self.confirm();
								self.stepIndex += num;
							} else if(valid === false && requiredNodeData.length < 3) {
								self.$notify({
									title: self.lang.base.warning,
									message: self.lang.base.errorNodeInfo,
									type: 'warning'
								});
							}
							if(valid) {
								self.confirm();
								self.stepIndex += num;
							}
						})
					} else if(self.stepIndex === 1) {
						self.$refs.form.validate((valid) => {
							if(valid) {
								if(self.deployType == 'kvm' && self.isShowNetwork === true) {
									if(self.checkBtnDisable === false) {
										self.$notify({
											title: '警告',
											message: '请点击检测账号信息',
											type: 'warning'
										});
										return false;
									}
									if(self.checkedIndex === 1) {
										self.checkIp(num)
									} else {
										if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
											self.stepIndex += num;
											self.verifyBigdata()
											self.getNicInfo()
											self.getSeparate()
										} else {
											sessionStorage.setItem('cloudInfo', JSON.stringify(self.form))
											self.stepIndex = 3
											setTimeout(function() {
												self.cloudInfo = JSON.parse(sessionStorage.getItem('cloudInfo'))
												self.ciphertextPassword = ''
												for(let i = 0; i < self.cloudInfo.password.length; i++) {
													self.ciphertextPassword += '*'
												}
											}, 500)
										}
									}
								} else {
									if(self.checkBtnDisable === false) {
										self.$notify({
											title: '警告',
											message: '请点击检测账号信息',
											type: 'warning'
										});
										return false;
									}
									if(self.checkBtnDisable === true) {
										self.postPanacube()
										if(self.checkedIndex === 1) {
											self.stepIndex += num;
										} else {
											if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
												self.stepIndex += num;
											} else {
												self.stepIndex = 3
												setTimeout(function() {
													self.cloudInfo = JSON.parse(sessionStorage.getItem('cloudInfo'))
													self.ciphertextPassword = ''
													for(let i = 0; i < self.cloudInfo.password.length; i++) {
														self.ciphertextPassword += '*'
													}
												}, 500)
											}
										}
										self.verifyBigdata()
										//										self.getNodes()
										self.getNicInfo()
										self.getSeparate()
									}
								}
							}
						})

					} else if(self.stepIndex === 0) {
						if(self.checkedIndex === 1) {
							self.stepIndex += num;
						} else {
							if(self.deployType === 'kvm') {
								if(self.checkUpdateList.indexOf('deploy_kvm') > -1) {
									self.stepIndex += num;
								} else if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
									self.stepIndex += num;
									self.isShowNetwork = false
								} else if(self.checkUpdateList.indexOf('update_storage_image') > -1) {
									self.stepIndex += num;
									self.isShowNetwork = false
								}
							} else {
								self.stepIndex += num;
							}
						}
					} else {
						self.stepIndex += num;
					}
				} else {
					if(self.stepIndex === 0) {
						self.showInstallUpgrading = false
						self.showUpgrade = false
						self.showInstallationSource = true
					} else if(self.stepIndex === 3) {
						if(self.checkedIndex === 1) {
							self.stepIndex += num;
						} else {
							if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
								self.stepIndex += num;
							} else {
								self.stepIndex = 1
							}
						}
					} else if(self.stepIndex === 1) {
						if(self.checkedIndex === 1) {
							self.stepIndex += num;
							self.showInstallationSource = true
						} else {
							self.stepIndex += num;
						}
					} else {
						self.stepIndex += num;
					}
				}
			},
			skip() {
				this.stepIndex = 3
				sessionStorage.setItem('cloudInfo', JSON.stringify(this.form))
			},
			choseMode(index, deployType) {
				this.activeIndex = index
				this.showInstallationSource = true
				this.showMode = false
				this.deployType = deployType.toLowerCase()
				this.choseModes(deployType.toLowerCase())
				this.getUsbList()
			},
			choseModes(deployType) {
				this.$http({
					url: this.api.post_deploy_type,
					method: 'post',
					data: {
						type: deployType
					}
				}).then(function(res) {

				}, function(res) {

				});
			},
			getDeployStatus() {
				let self = this
				this.$http({
					url: this.api.get_deploy_status,
					method: 'get'
				}).then(function(res) {
					if(res.data.data != '') {
						let data = JSON.parse(res.data.data)
						self.isInstalled = true
						self.checkedIndex = 2
					} else {
						self.checkedIndex = 1
					}
				}, function(res) {

				});
			},
			getUsbList() {
				let self = this
				this.$http({
					url: this.api.get_usb,
					method: 'get'
				}).then(function(res) {
					self.showInstallationSources = res.data
				}, function(res) {

				});
			},
			installPrev() {
				this.showInstallationSource = false
				this.showMode = true
			},
			installNext() {
				this.showInstallationSource = false
				//				this.showInstallUpgrading = true
				if(this.isInstalled === true) {
					this.showUpgrade = true
				} else {
					this.stepIndex = 1
				}
			},
			//			selectBtn(index) {
			//				this.checkedIndex = index
			//				if(index === 2) {
			//					this.showUpgrade = true
			//					this.showInstallUpgrading = false
			//				}
			//			},
			//			筋斗云配置

			//			验证账号
			verifyPw() {
				let self = this
				let Base64 = require('js-base64').Base64;
				this.$refs.form.validate((valid) => {
					if(valid) {
						this.$http({
							url: this.api.post_account,
							method: 'post',
							data: {
								username: this.form.username,
								password: Base64.encode(this.form.password)
								//								password: this.form.password
							}
						}).then(function(res) {
							if(res.data.code === 0) {
								self.checkBtnDisable = true
								self.errorDialogVisible = false
								self.disableCheck = true
								self.showNetworkClick()
							} else if(res.data.code === 1) {
								self.disableCheck = false
								self.errorDialogVisible = true
							}
						}, function(res) {

						});
					}
				})
			},
			showNetworkClick() {
				this.showNetwork = true
				this.getPublicNetwork()
			},
			errorClick() {
				this.errorDialogVisible = false
			},
			getPublicNetwork() {
				let self = this
				this.$http({
					url: this.api.get_panacube,
					method: 'get',
				}).then(function(res) {
					self.publicNetwork = res.data.data
					if(self.publicNetwork.length > 0) {
						self.pubnetIndex = 0
						self.changePub()
					}
					self.getJumpLink()
				}, function(res) {

				});
			},
			//			改变公网
			changePub() {
				let subnetData = this.publicNetwork[this.pubnetIndex]
				this.subnetName = subnetData.subnets
				this.pubnetworkId = subnetData.id
				if(this.subnetName.length > 0) {
					this.sunetIndex = 0
					this.changeSubnet()
				}
			},
			//			改变子网
			changeSubnet() {
				let subnetSegment = this.subnetName[this.sunetIndex];
				this.networkId = subnetSegment.id;
				let name = subnetSegment.name,
					pools = subnetSegment.allocationPools;
				for(let i = 0; i < pools.length; i++) {
					pools[i].name = pools[i].start + " ~ " + pools[i].end;
				}
				this.subnetList = pools;
				if(this.subnetList.length > 0) {
					this.segment = 0
					this.changeSegment()
				}
			},
			changeSegment() {
				let segments = this.subnetList[this.segment],
					start = segments.start,
					end = segments.end;
				this.setConsoleIp(start, end);
			},
			// 设置控制台ip
			setConsoleIp: function(start, end) {
				let startArr = start.split('.'),
					endArr = end.split('.'),
					obj = {},
					res = '',
					disable = [],
					placeholder = [],
					first = true;
				startArr.map(function(item, key) {
					if(item === endArr[key]) {
						obj[key] = item;
						res = res + item + '.';
						disable.push(parseInt(key));
						placeholder.push('');
					} else {
						obj[key] = {
							start: item,
							end: endArr[key]
						};
						if(first === true) {
							first = false;
							placeholder.push(startArr[key] + '~' + endArr[key])
						} else {
							placeholder.push('0~255');
						}
					}
				});
				this.form.consoleIp = res;
				this.ipRange = obj;
				if(this.deployType === 'kvm' && this.checkedIndex === 2) {
					this.disabledArr = [0, 1, 2, 3];
				} else {
					this.disabledArr = disable;
				}
				this.placeholderArr = placeholder;
				this.ipValidMessage = this.common.formatMsg(this.lang.base.consoleIpRange, {
					start: start,
					end: end
				});
			},
			//          验证控制台ip
			validateGateway: function(rule, value, callback) {
				if(value == null || value === '') {
					callback();
				} else {
					let ipArr = value.split("."),
						firstFlag = true,
						flag = true;
					for(let key in this.ipRange) {
						if(this.ipRange.hasOwnProperty(key)) {
							let num = parseInt(ipArr[parseInt(key)]);
							if(this.ipRange[key].toString() === '[object Object]') {
								if(firstFlag === true) {
									firstFlag = false;
									if(num < this.ipRange[key].start || num > this.ipRange[key].end) {
										flag = false;
										break;
									}
								} else {
									if(parseInt(key) === 3) {
										if(num < 0 || num > this.ipRange[key].end) {
											flag = false;
											break;
										}
									} else {
										if(num < 0 || num > 255) {
											flag = false;
											break;
										}
									}
								}
							}
						}
					}
					if(flag === true) {
						callback();
					} else {
						callback(new Error(rule.message));
					}
				}
			},
			//			检测ip是否被占用
			checkIp(num) {
				let self = this
				this.$refs.form.validate((valid) => {
					if(valid) {
						this.$http({
							url: this.api.get_verify_ip + this.common.formatGetUrl({
								ip: this.form.consoleIp,
								network_id: this.pubnetworkId
							}),
							method: 'get',
						}).then(function(res) {
							if(res.data.code === 0) {
								self.checkConsoleIp = true
								if(res.data.data === 'null') {
									self.ipIsOccupied = true
								} else {
									self.ipIsOccupied = false
									if(self.checkBtnDisable === true && self.checkConsoleIp === true) {
										self.postPanacube()
										if(self.checkedIndex === 1) {
											self.stepIndex += num;
										} else {
											if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
												self.stepIndex += num;
											} else {
												self.stepIndex = 3
												setTimeout(function() {
													self.cloudInfo = JSON.parse(sessionStorage.getItem('cloudInfo'))
													self.ciphertextPassword = ''
													for(let i = 0; i < self.cloudInfo.password.length; i++) {
														self.ciphertextPassword += '*'
													}
												}, 500)
											}
										}
										self.verifyBigdata()
										//										self.getNodes()
										self.getNicInfo()
										self.getSeparate()
									}
									//									self.$notify({
									//										title: self.lang.common.success,
									//										message: self.lang.common.ipSuccess,
									//										type: 'success'
									//									});

								}
							}
						}, function(res) {

						});
					}
				})
			},
			//			控制台ip改变就要验证ip是否被占用
			changeConsole() {
				this.checkConsoleIp = false
				this.ipIsOccupied = false
				this.$refs.form.validateField('consoleIp');
			},
			//			点击下一步把配置大数据的参数传给后端
			postPanacube() {
				let data = {},
					self = this
				if(this.deployType === 'kvm') {
					data = {
						username: this.form.username,
						password: this.form.password,
						panacube_ip: this.form.consoleIp,
						network_id: this.pubnetworkId
					}
				} else {
					data = {
						username: this.form.username,
						password: this.form.password
					}
				}
				this.$http({
					url: this.api.post_panacube,
					method: 'post',
					data: data
				}).then(function(res) {
					if(res.data.code === 0) {
						sessionStorage.setItem('cloudInfo', JSON.stringify(self.form))
					}
				}, function(res) {

				});
			},
			//			检查大数据是否被安装过
			verifyBigdata() {
				let self = this
				this.$http({
					url: this.api.post_verify_bigdata,
					method: 'post',
					data: {
						"inspect": 'bigdata'
					}
				}).then(function(res) {
					self.bigdataForm.nodeData = []
					if(res.data.code == 0) {
						if(res.data.data.length > 0) {
							self.deployedData = true
							self.alreadyExist = res.data.data
						} else {
							self.getNodes()
						}
					}

				}, function(res) {

				})
			},
			//			不接着部署
			cancleDeployedData() {
				this.deployedData = false
				this.getNodes()
			},
			//			接着部署
			ensureDeployedData() {
				let self = this
				self.deployedData = false
				self.bigdataForm.nodeData = []
				this.alreadyExist.forEach(function(item) {
					if(item.dev_path.length > 0) {
						self.deployMethod = 2
					}
					self.bigdataForm.nodeData.push({
						cpu: item.cpu,
						mem: item.mem,
						bigdata: item.bigdata,
						netmask: item.netmask,
						network_ip: item.network_ip,
						network_name: item.network_name,
						nodeName: item['node-name'],
						ip: item.ip,
						dev_path: item.dev_path,
						diskList: [],
						diskIdList: []
					})
				})
				self.bigdataForm.nodeData.sort(self.objSort('nodeName'))
				self.activeName = self.bigdataForm.nodeData[0]['nodeName']
				self.bigdataForm.nodeData.forEach(function(item) {
					self.getOneNodeInfo(item.ip)
				})
			},
			//			大数据配置页面
			getNodes() {
				let self = this
				this.$http({
					url: this.api.get_nodes,
					method: 'get',
				}).then(function(res) {
					self.bigdataForm.nodeData = []
					res.data.data.forEach(function(item) {
						self.bigdataForm.nodeData.push({
							cpu: '',
							mem: '',
							bigdata: '',
							network_ip: '',
							network_name: '',
							netmask: '',
							nodeName: self.deployType === 'docker' ? item['node-name'] : item.hostName,
							ip: self.deployType === 'docker' ? item.ip : item.hostInfoMap.ip,
							dev_path: [],
							diskList: [],
							diskIdList: []
						})
					})
					self.nodesList = res.data.data
					self.ip = self.deployType === 'docker' ? res.data.data[0].ip : res.data.data[0].hostInfoMap.ip
					if(self.deployType === 'docker') {
						self.nodesList.forEach(function(item) {
							self.getOneNodeInfo(item.ip)
						})
						self.bigdataForm.nodeData.sort(self.objSort('nodeName'))
					} else {
						self.nodesList.forEach(function(item) {
							self.getOneNodeInfo(item.hostInfoMap.ip)
						})
						self.bigdataForm.nodeData.sort(self.objSort('nodeName'))
					}
					self.activeName = self.bigdataForm.nodeData[0].nodeName
				}, function(res) {

				});
			},
			//			获取网卡信息
			getNicInfo() {
				let self = this
				this.$http({
					url: this.api.get_nic,
					method: 'get',
				}).then(function(res) {
					self.nicList = res.data.exits_network_name
				}, function(res) {

				});
			},
			//			获取某个节点的信息
			getOneNodeInfo(ip) {
				let self = this
				this.$http({
					url: this.api.get_node_info + this.common.formatGetUrl({
						ip: ip
					}),
					method: 'get',
				}).then(function(res) {
					self.bigdataForm.nodeData.forEach(function(item) {
						if(item.ip === ip) {
							item.cpuUsed = parseInt(res.data.data.cpu.split('/')[0])
							item.cpuTotal = parseInt(res.data.data.cpu.split('/')[1])
							item.memUsed = parseInt(res.data.data.mem.split('/')[0])
							item.memTotal = parseInt(res.data.data.mem.split('/')[1])
							item.diskUsed = parseInt(res.data.data.disk.split('/')[0])
							item.diskTotal = parseInt(res.data.data.disk.split('/')[1])
						}
					})
					self.storageTotal = parseInt(res.data.data.disk.split('/')[1])
					self.storageUsed += parseInt(res.data.data.disk.split('/')[0])
					self.storagePrecent = (self.storageUsed / self.storageTotal) * 100
					self.cpu = res.data.data.cpu
					self.mem = res.data.data.mem
					self.disk = res.data.data.disk
					self.cpuPrecent = parseInt(res.data.data.cpu.split('/')[0]) / parseInt(res.data.data.cpu.split('/')[1]) * 100
