			deployBigData() {
				let self = this
				this.$http({
					url: this.api.post_deploy_bigdatainfo,
					method: 'post',
				}).then(function(res) {
					if(res.data.code === 0) {
						self.bigdataUp = true
						if(self.checkedIndex === 1) {
							if(self.deployType === 'kvm') {
								self.deployCloud()
							} else {
								self.updateDb()
							}
						} else {
							if(self.deployType === 'kvm') {
								if(self.checkUpdateList.indexOf('deploy_kvm') > -1) {
									//									self.deployCloud()
									self.updateCloud()
								}
							} else {
								self.updateDb()
								clearInterval(self.timer)
								clearInterval(self.interval)
							}
						}
					}
				}, function(res) {
					clearInterval(self.timer)
					clearInterval(self.interval)
				});
			},
			deployStorageImage() {
				let self = this
				this.$http({
					url: this.api.post_deploy_storage_image,
					method: 'post',
				}).then(function(res) {
					if(res.data.code === 0) {
						self.mirroringUp = true
						if(self.checkedIndex === 1) {
							self.deployBigData()
						} else {
							if(self.deployType === 'kvm') {
								if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
									self.deployBigData()
								} else if(self.checkUpdateList.indexOf('deploy_kvm') > -1) {
									//									self.deployCloud()
									self.updateCloud()
								} else {
									self.finishBtn = false
									clearInterval(self.timer)
									clearInterval(self.interval)
								}
							} else {
								if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
									self.deployBigData()
								}
							}
						}
					}
				}, function(res) {
					clearInterval(self.timer)
					clearInterval(self.interval)
				});
