					url: this.api.post_deploy_docker,
					method: 'post',
				}).then(function(res) {
					if(res.data.code === 0) {
						self.dockerUpgrade = true
						if(self.checkedIndex === 1) {
							if(self.deployType === 'docker') {
								self.deployStorageImage()
							}
						} else {
							if(self.checkUpdateList.indexOf('update_storage_image') > -1) {
