						//							self.getDeployProcess()
						//						}, 3000);
					} else {
						if(self.checkUpdateList.indexOf('update_docker') > -1) {
							self.deployDocker()
						} else if(self.checkUpdateList.indexOf('update_storage_image') > -1) {
							self.deployStorageImage()
						} else if(self.checkUpdateList.indexOf('update_bigdata') > -1) {
							self.deployBigData()
						}
						let HH = 0;
						let mm = 0;
						let ss = 0;
						let str = '';
						self.timer = setInterval(function() {
							str = "";
							if(++ss == 60) {
								if(++mm == 60) {
									HH++;
									mm = 0;
								}
								ss = 0;
							}
							str += HH < 10 ? "0" + HH : HH;
							str += ":";
							str += mm < 10 ? "0" + mm : mm;
							str += ":";
							str += ss < 10 ? "0" + ss : ss;
							document.getElementById("timetext").innerHTML = str;
						}, 1000);
						self.getDeployProcess()
						//						self.interval = setInterval(function() {
						//							self.getDeployProcess()
						//						}, 3000);
					}
				}
			},
