					if(this.deployType === 'kvm') {
						this.checkUpdateList = ['update_storage_image', 'update_bigdata', 'deploy_kvm']
					} else {
						this.checkUpdateList = ['update_storage_image', 'update_bigdata', 'update_docker']
					}
				} else {
					this.checkUpdateList = []
				}
			},
			jumpCloud() {
				window.open(this.jumpIp)
			},
			//			对节点进行排序
			objSort(prop) {
				return function(obj1, obj2) {
					var val1 = obj1[prop];
					var val2 = obj2[prop];
					if(!isNaN(Number(val1)) && !isNaN(Number(val2))) {
						val1 = Number(val1);
						val2 = Number(val2);
					}
					if(val1 < val2) {
						return -1;
					} else if(val1 > val2) {
						return 1;
					} else {
						return 0;
					}
				}
			},
			//			判断某个值在数组中的下标
