<style scoped="scoped">
	.container {
		margin-top: 100px;
	}
	/*步骤条样式*/
	
	.steps {
		margin: 20px 0;
		width: 100%;
		border-bottom: 1px solid #4297cd;
		padding-bottom: 30px;
	}
	
	.step01,
	.step02,
	.step03,
	.step04,
	.step05 {
		width: 364px;
		height: 60px;
		display: inline-block;
		background: url(/static/image/step1.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.step02 {
		background: url(/static/image/step2.png) no-repeat;
	}
	
	.step03 {
		background: url(/static/image/step3.png) no-repeat;
	}
	
	.step04 {
		background: url(/static/image/step4.png) no-repeat;
	}
	
	.step05 {
		background: url(/static/image/step5.png) no-repeat;
	}
	/*中间内容*/
	
	.content {
		position: relative;
		width: 1500px;
		height: 600px;
		margin: 0 auto;
	}
	
	.choice-mode {
		width: 930px;
		margin: 155px auto 0;
	}
	
	.mode-box {
		position: relative;
		width: 366px;
		height: 474px;
		background: #fff;
		box-shadow: 0 0 15px 0 rgba(0, 0, 0, .05);
		float: left;
		cursor: pointer;
	}
	
	.mode-box.active {
		background-image: linear-gradient(#5fbfaa 20%, #47a0c6 80%);
	}
	
	.mode-box:nth-child(2) {
		margin-left: 190px;
	}
	
	.mode-title {
		width: 130px;
		height: 130px;
		border-radius: 50%;
		background: #fff;
		text-align: center;
		line-height: 130px;
		font-size: 18px;
		font-weight: 400;
		box-shadow: 0 0 15px 0 rgba(0, 0, 0, .1);
		margin: -65px auto 0;
	}
	
	.mode-desc {
		padding: 66px;
		font-size: 16px;
		line-height: 34px;
	}
	
	.mode-desc:active {
		color: #fff;
	}
	
	.checkbox {
		position: absolute;
		left: 50%;
		bottom: 40px;
		margin-left: -20px;
		width: 40px;
		height: 40px;
		background-size: 100% 100%;
	}
	
	.nocheck {
		background: url(/static/image/nocheck.png) no-repeat;
	}
	
	.checked {
		background: url(/static/image/checked.png) no-repeat;
	}
	/*选择安装源*/
	
	.installation-source {
		text-align: center;
		margin: 100px auto;
	}
	
	.btnstyle {
		position: fixed;
		display: block;
		right: 100px;
		bottom: 100px;
	}
	/*选择全部安装还是升级部分功能*/
	
	.install-upgrading ul {
		width: 500px;
		list-style-type: none;
		margin-top: 140px;
		margin: 140px auto 0;
	}
	
	.install-upgrading ul li {
		margin-top: 90px;
		width: 502px;
		height: 80px;
		cursor: pointer;
	}
	
	.install-upgrading ul li:nth-child(1) {
		background: url(/static/image/install.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.install-upgrading ul li:nth-child(1).active {
		background: url(/static/image/install-checked.png) no-repeat;
	}
	
	.install-upgrading ul li:nth-child(2) {
		background: url(/static/image/upgraing.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.install-upgrading ul li:nth-child(2).active {
		background: url(/static/image/upgraing-checked.png) no-repeat;
	}
	
	.footerright {
		position: fixed;
		right: 100px;
		bottom: 50px;
	}
	/*筋斗云配置页面的样式*/
	
	.cloudDiv {
		width: 1200px;
		margin: 90px auto 0;
	}
	
	.alert-content {
		text-align: center;
		font-size: 16px;
	}
	
	.alert-content .el-icon-circle-check {
		color: #4fbead;
		font-size: 50px;
	}
	
	.alert-content .el-icon-warning {
		color: red;
		font-size: 50px;
	}
	
	.cloudDiv .pana-ip-input {
		min-width: 220px;
	}
	/*大数据配置页面样式*/
	
	.bigData {
		width: 1200px;
		margin: 80px auto 0;
	}
	
	.bigData-top {
		width: 600px;
	}
	
	.bigData .progress {
		width: 300px;
		display: inline-block;
		margin-left: 20px;
	}
	
	.bigData .used {
		color: #999;
		margin-left: 20px;
	}
	
	.bigData .deploy-method {
		margin-top: 40px;
	}
	
	.bigData .radio-group {
		margin-left: 20px;
	}
	
	.bigData .bigdata-content {
		margin-top: 60px;
	}
	
	.bigData .split {
		border: 1px solid #e0e0e;
	}
	
	.bigdata-tab .el-tabs__content {
		height: 510px;
	}
	/*确认信息页面的样式*/
	
	.confirm-info .cloud-info {
		list-style-type: none;
		height: 50px;
		background: #edf0f6;
		margin-top: 20px;
		overflow: hidden;
	}
	
	.confirm-info .cloud-info li {
		width: 15%;
		line-height: 50px;
		font-size: 16px;
		color: #666;
		float: left;
	}
	
	.confirm-info .cloud-info li span {
		padding-left: 10px;
	}
	
	.confirm-info .confirm-info h5 {
		color: #666;
	}
	
	.confirm-info .cloud-table {
		margin-top: 20px;
	}
	/*升级页面*/
	
	.upgrade-div {
		width: 1010px;
		margin: 90px auto 0;
		overflow: hidden;
	}
	
	.upgrade-div .left {
		width: 358px;
		height: 400px;
		float: left;
		border-right: 1px solid #e0e0e0;
	}
	
	.upgrade-div .right {
		float: left;
		margin-left: 45px;
	}
	
	.checkbox-div {
		width: 600px;
		margin-top: 42px;
	}
	
	.checkbox-desc {
		height: 100px;
		margin-left: 10px;
		overflow: hidden;
	}
	
	.checkbox-desc .lefts {
		float: left;
	}
	
	.checkbox-desc .lefts img {
		width: 40px;
		margin-top: 10px;
	}
	
	.checkbox-desc .rights {
		float: left;
		width: 470px;
		margin-left: 20px;
	}
	
	.checkbox-desc .rights h6 {
		font-size: 16px;
		color: #666;
	}
	
	.checkbox-desc .rights p {
		width: 470px;
		word-break: break-word;
	}
	
	.upgrade-div .el-checkbox__input {
		margin-top: -80px;
	}
	
	.upgrad-btn {
		list-style-type: none;
		margin-top: 74px;
	}
	
	.upgrad-btn li {
		cursor: pointer;
	}
	
	.upgrad-btn li:nth-child(1) {
		width: 253px;
		height: 57px;
		background: url(/static/image/section-upgrad3.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.upgrad-btn li:nth-child(2) {
		width: 253px;
		height: 57px;
		background: url(/static/image/section-upgrad2.png) no-repeat;
		background-size: 100% 100%;
		margin-top: 48px;
	}
	
	.upgrad-btn li:nth-child(1).active {
		background: url(/static/image/section-upgrad1.png) no-repeat;
	}
	
	.upgrad-btn li:nth-child(2).active {
		background: url(/static/image/section-upgrad4.png) no-repeat;
	}
	/*部署页面*/
	
	.wheel-planting {
		width: 1500px;
		height: 300px;
	}
	
	#deploy {
		width: 250px;
		height: 250px;
		/*background: pink;*/
	}
	
	.deploy-content {
		margin-top: 115px;
		margin-left: 240px;
	}
	
	.duration {
		position: absolute;
		left: 500px;
		top: 500px;
	}
	
	.duration #txt {
		font-size: 16px;
		color: #666;
	}
	
	.duration #txt span {
		color: #4398cb;
		font-size: 18px;
		padding-left: 10px;
	}
	
	.deploy-status {
		position: absolute;
		left: 800px;
		top: 550px;
	}
	
	.switch-btn {
		margin-right: 10px;
	}
</style>
<style type="text/css">
	/*步骤条的样式修改*/
	
	.el-step__icon.is-text {
		border: none !important;
	}
	
	.el-step.is-simple .el-step__icon {
		width: 364px !important;
		height: 60px !important;
	}
	
	.el-steps--simple {
		background: #fff !important;
		padding: 0 !important;
	}
	
	.el-step.is-simple .el-step__arrow {
		display: none !important;
	}
	
	.el-step__head.is-process .step02 {
		background: url(/static/image/step2-active.png) no-repeat;
	}
	
	.el-step__head.is-finish .step02 {
		background: url(/static/image/step2-active.png) no-repeat;
	}
	
	.el-step__head.is-process .step03 {
		background: url(/static/image/step3-active.png) no-repeat;
	}
	
	.el-step__head.is-finish .step03 {
		background: url(/static/image/step3-active.png) no-repeat;
	}
	
	.el-step__head.is-process .step04 {
		background: url(/static/image/step4-active.png) no-repeat;
	}
	
	.el-step__head.is-finish .step04 {
		background: url(/static/image/step4-active.png) no-repeat;
	}
	
	.el-step__head.is-process .step05 {
		background: url(/static/image/step5-active.png) no-repeat;
	}
	
	.el-step__head.is-finish .step05 {
		background: url(/static/image/step5-active.png) no-repeat;
	}
	
	.upgrade-div .el-checkbox__label {
		vertical-align: top;
	}
	
	.upgrade-div .el-checkbox {
		white-space: normal;
	}
	
	.el-step.is-simple {
		width: 20% !important;
	}
</style>


