// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import '../static/css/common.css'
import '../static/css/base.css'
import '@/assets/css/element-variables.scss'
import 'font-awesome/css/font-awesome.css'
import lang from '@/config/i18n/zh-cn'
import common from './util/common'
import axios from 'axios'
import axiosConf from '../static/config/host'
import apiConf from '../static/config/api'
import echarts from 'echarts'
import md5 from 'js-md5'
Vue.config.productionTip = false

Vue.prototype.$echarts = echarts;
Vue.prototype.lang = lang;
// 引入api配置文件
Vue.prototype.api = apiConf;
Vue.prototype.host = axiosConf.baseURL;
// 引入axios
Vue.prototype.$http = axios.create(axiosConf);
// 引入公共函数
Vue.prototype.common = common;
//引入md5
Vue.prototype.$md5 = md5;
// 配置静态资源位置
Vue.prototype.STATIC_URL = '../static/';
Vue.use(Element);
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



