<template>
	<div class="bigData">
		<div class="bigData-top">
			<span>{{lang.base.totalStorageSpace}}</span>
			<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="storagePrecent"></el-progress>
			<span class="used">{{lang.base.used}}：{{storageUsed}}/{{storageTotal}}GB</span>
		</div>
		<div class="deploy-method">
			<span>{{lang.base.deployMethod}}</span>
			<el-radio-group class="radio-group" v-model="deployMethod">
				<el-radio :label="1">{{lang.base.ceph}}</el-radio>
				<el-radio :label="2">{{lang.base.bareDisk}}</el-radio>
			</el-radio-group>
		</div>
		<div class="bigdata-content">
			<el-form ref="bigdataForm" :model="bigdataForm" :rules="rules">
				<el-form-item label="" prop="">
					<el-tabs v-if="bigdataForm.nodeData.length>0" v-model="activeName" tab-position="left" style="height: 600px;" @tab-click="handleClick">
						<el-tab-pane :label="item.nodeName" :name="item.nodeName" :key="index" v-for="(item, index) in bigdataForm.nodeData">
							<el-row>
								<el-col>
									<span style="padding-left: 60px;">{{item.nodeName}}</span>
								</el-col>
							</el-row>
							<el-row style="margin-top: 30px;">
								<el-col :span="12">
									<el-form-item :prop="'nodeData.'+ index +'.network_name'" :rules="[{required: true,message:lang.common.required, trigger:'blur'}]" :label="lang.base.nic" label-width="120px">
										<el-select v-model="item.network_name" style='width: 100%;' @change="changeNetworkName">
											<el-option v-for='item in nicList' :key="item.network_name" :value="item.network_name" :label="item.network_name"></el-option>
										</el-select>
									</el-form-item>
								</el-col>
								<el-col :span="12">
									<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{
							validator: common.validateIpAddress,
							message: lang.common.ipAddressAlert,
							trigger: 'blur'
						}]" :prop="'nodeData.' + index + '.network_ip'" :label="lang.base.ipAddress" label-width="120px">
										<pana-ip-input v-model="item.network_ip"></pana-ip-input>
									</el-form-item>
								</el-col>
							</el-row>
							<el-row style="margin-top: 30px;border-bottom: 1px solid #e0e0e0;padding-bottom: 30px;">
								<el-col :span="12">
									<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{
							validator: common.validateNetMask,
							message: lang.base.netMaskAlert,
							trigger: 'blur'
						}]" :prop="'nodeData.' + index + '.netmask'" :label="lang.base.subnetMask" label-width="120px">
										<pana-ip-input v-model="item.netmask"></pana-ip-input>
									</el-form-item>
								</el-col>
							</el-row>
							<el-row>
								<el-col>
									<div style="margin-left: 60px;margin-top: 20px;">{{lang.base.distributionNode}}</div>
								</el-col>
							</el-row>
							<el-row style="margin-top: 30px;">
								<el-col :span="12">
									<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.cpuTotal - item.cpuUsed,
                                            message: (item.cpuTotal - item.cpuUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: 'cpu'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.cpuTotal - item.cpuUsed})}]" :prop="'nodeData.' + index + '.cpu'" :label="lang.base.cpu" label-width="120px">
										<el-input type="number" v-model.number="item.cpu">
										</el-input>
									</el-form-item>
								</el-col>
								<el-col :span="7">
									<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.cpuUsed/item.cpuTotal)*100)"></el-progress>
								</el-col>
								<el-col :span="5">
									<span>{{lang.base.used}}:{{item.cpuUsed}}/{{item.cpuTotal}}{{lang.base.nucleus}}</span>
								</el-col>
							</el-row>
							<el-row style="margin-top: 30px;">
								<el-col :span="12">
									<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.memTotal - item.memUsed,
                                            message: (item.memTotal - item.memUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: '内存'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.memTotal - item.memUsed})}]" :prop="'nodeData.' + index + '.mem'" :label="lang.base.memory" label-width="120px">
										<el-input type="number" v-model.number="item.mem"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="7">
									<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.memUsed/item.memTotal)*100)"></el-progress>
								</el-col>
								<el-col :span="5">
									<span>{{lang.base.used}}:{{item.memUsed}}/{{item.memTotal}}GB</span>
								</el-col>
							</el-row>
							<el-row v-if="deployMethod===1" style="margin-top: 30px;">
								<el-col :span="12">
									<el-form-item :rules="[
                    {required: true,message:lang.common.required, trigger:'blur'},{ type: 'integer', min: 1, max: item.diskTotal - item.diskUsed,
                                            message: (item.diskTotal - item.diskUsed) === 0
                                                ? common.formatMsg(lang.base.noResource, {item: '存储'})
                                                : common.formatMsg(lang.base.maxInter, {max: item.diskTotal - item.diskUsed})}]" :prop="'nodeData.' + index + '.bigdata'" :label="lang.base.storage" label-width="120px">
										<el-input type="number" v-model.number="item.bigdata"></el-input>
									</el-form-item>
								</el-col>
								<el-col :span="7">
									<el-progress class="progress" :stroke-width="10" :show-text="false" :percentage="Math.ceil((item.diskUsed/item.diskTotal)*100)"></el-progress>
								</el-col>
								<el-col :span="5">
									<span>{{lang.base.used}}:{{item.diskUsed}}/{{item.diskTotal}}GB</span>
								</el-col>
							</el-row>
							<el-row v-if="deployMethod===2" style="margin-top: 30px;">
								<el-form-item :label="lang.base.storage" label-width="120px">
									<el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
									<div style="margin: 15px 0;"></div>
									<el-checkbox-group v-model="checkedList" @change="handleCheckedCitiesChange">
										<el-checkbox v-for="s in diskList" :label="s.file_id" :key="s.file_id">{{s.dev}}</el-checkbox>
									</el-checkbox-group>
								</el-form-item>
							</el-row>
						</el-tab-pane>
					</el-tabs>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import ipInput from '@/components/ipInput'
	export default {
		components: {
			'pana-ip-input': ipInput
		},
		data: function() {
			return {
				deployMethod: 1,
				nodesList: [],
				checkAll: false,
				checkedList: [],
				diskList: [],
				diskIdList: [],
				isIndeterminate: true,
				nicList: [],
				selectNic: '',
				ip: '',
				cpu: '',
				disk: '',
				mem: '',
				activeName: '',
				storageTotal: 0,
				storageUsed: 0,
				storagePrecent: 0,
				cpuPrecent: 0,
				memPrecent: 0,
				diskPrecent: 0,
				bigdataForm: {
					nodeData: []
				},
				rules: {}
			}
		},
		methods: {
			getNodes() {
				let self = this
				this.$http({
					url: this.api.get_nodes,
					method: 'get',
				}).then(function(res) {
					res.data.data.forEach(function(item) {
						self.bigdataForm.nodeData.push({
							cpu: '',
							mem: '',
							bigdata: '',
							network_ip: '',
							network_name: '',
							netmask: '',
							nodeName: item.hostName,
							ip: item.hostInfoMap.ip,
							dev_list: []
						})
					})
					self.nodesList = res.data.data
					self.ip = res.data.data[0].hostInfoMap.ip
					self.nodesList.forEach(function(item) {
						self.getOneNodeInfo(item.hostInfoMap.ip)
					})
					self.activeName = res.data.data[0].hostName
				}, function(res) {

				});
			},
			//			获取网卡信息
			getNicInfo() {
				let self = this
				this.$http({
					url: this.api.get_nic,
					method: 'get',
				}).then(function(res) {
					self.nicList = res.data.exits_network_name
				}, function(res) {

				});
			},
			//			获取某个节点的信息
			getOneNodeInfo(ip) {
				let self = this
				this.$http({
					url: this.api.get_node_info + this.common.formatGetUrl({
						ip: ip
					}),
					method: 'get',
				}).then(function(res) {
					self.bigdataForm.nodeData.forEach(function(item) {
						if(item.ip === ip) {
							item.cpuUsed = parseInt(res.data.data.cpu.split('/')[0])
							item.cpuTotal = parseInt(res.data.data.cpu.split('/')[1])
							item.memUsed = parseInt(res.data.data.mem.split('/')[0])
							item.memTotal = parseInt(res.data.data.mem.split('/')[1])
							item.diskUsed = parseInt(res.data.data.disk.split('/')[0])
							item.diskTotal = parseInt(res.data.data.disk.split('/')[1])
						}
					})
					self.storageTotal += parseInt(res.data.data.disk.split('/')[1])
					self.storageUsed += parseInt(res.data.data.disk.split('/')[0])
					self.storagePrecent = (self.storageUsed / self.storageTotal) * 100
					self.cpu = res.data.data.cpu
					self.mem = res.data.data.mem
					self.disk = res.data.data.disk
					self.cpuPrecent = parseInt(res.data.data.cpu.split('/')[0]) / parseInt(res.data.data.cpu.split('/')[1]) * 100
					self.memPrecent = parseInt(res.data.data.mem.split('/')[0]) / parseInt(res.data.data.mem.split('/')[1]) * 100
					self.diskPrecent = parseInt(res.data.data.disk.split('/')[0]) / parseInt(res.data.data.disk.split('/')[1]) * 100
				}, function(res) {

				});
			},
			handleClick() {
				let self = this
			},
			//			获取磁盘列表
			getSeparate() {
				let self = this
				this.$http({
					url: this.api.get_separate,
					method: 'get',
				}).then(function(res) {
					res.data.forEach(function(item) {
						self.diskIdList.push(item.file_id)
					})
					self.diskList = res.data
				}, function(res) {

				});
			},
			handleCheckAllChange(val) {
				this.checkedList = val ? this.diskIdList : [];
				this.isIndeterminate = false;
			},
			handleCheckedCitiesChange(value) {
				let checkedCount = value.length;
				this.checkAll = checkedCount === this.diskIdList.length;
				this.isIndeterminate = checkedCount > 0 && checkedCount < this.diskList.length;
			},
			confirm() {
				let arr = [],
					self = this
				this.bigdataForm.nodeData.forEach(function(item) {
						arr.push({
							bigdata: item.bigdata,
							cpu: item.cpu,
							ip: item.ip,
							mem: item.mem,
							netmask: item.netmask,
							network_ip: item.network_ip,
							network_name: item.network_name,
							"node-name": item.nodeName,
						})
					}),
					this.$http({
						url: this.api.post_deploy_bigdata,
						method: 'post',
						data: {
							"node": arr
						}
					}).then(function(res) {
						if(res.data.code === 0) {
							sessionStorage.setItem('bigdata', JSON.stringify(self.bigdataForm.nodeData))
						}
					}, function(res) {

					});
			},
			changeNetworkName() {
				this.$refs.bigdataForm.validateField('nodeData.0.network_name');
				this.$refs.bigdataForm.validateField('nodeData.1.network_name');
				this.$refs.bigdataForm.validateField('nodeData.2.network_name');
			},
			getVal() {
				return this.$refs['bigdataForm']
			},
			returnJmp() {
				return true
			}
		},
		mounted() {
			this.getNodes()
			this.getNicInfo()
			this.getSeparate()
		}
	}
</script>

<style scoped="scoped">
	.bigData {
		width: 1200px;
		margin: 80px auto 0;
	}
	
	.bigData-top {
		width: 600px;
	}
	
	.bigData .progress {
		width: 300px;
		display: inline-block;
		margin-left: 20px;
	}
	
	.bigData .used {
		color: #999;
		margin-left: 20px;
	}
	
	.bigData .deploy-method {
		margin-top: 40px;
	}
	
	.bigData .radio-group {
		margin-left: 20px;
	}
	
	.bigData .bigdata-content {
		margin-top: 60px;
	}
	
	.bigData .split {
		border: 1px solid #e0e0e;
	}
</style>


