<template>
	<div class="cloudDiv">
		<el-form ref="form" :model="form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="10">
					<el-form-item :label="lang.base.account" prop="username">
						<el-input v-model="form.username" :placeholder="lang.base.accountPlaceholder"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="10">
					<el-form-item :label="lang.base.password" prop="password">
						<el-input type="password" v-model="form.password" :placeholder="lang.base.passwordPlaceholder"></el-input>
					</el-form-item>
				</el-col>
				<el-button type="primary" round size="small" style="margin-left: 10px;" @click="verifyPw">{{lang.base.check}}</el-button>
			</el-row>
			<!--<div>-->
			<div v-if="showNetwork===true">
				<el-row>
					<el-col :span="10">
						<el-form-item :label="lang.base.publicNetwork">
							<el-select v-model="pubnetIndex" placeholder="请选择" style="width: 100%;" @change="changePub">
								<el-option v-for="(item,key) in publicNetwork" :key="key" :value="key" :label="item.name"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="10">
						<el-form-item :label="lang.base.selectSubnet">
							<el-select v-model="sunetIndex" placeholder="请选择" style="width: 100%;" @change="changeSubnet">
								<el-option v-for="(item,key) in subnetName" :key="key" :value="key" :label="item.name"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="10">
						<el-form-item :label="lang.base.subnetSegment">
							<el-select v-model="segment" placeholder="请选择" style="width: 100%;">
								<el-option v-for="(item,key) in subnetList" :key="key" :value="key" :label="item.name"></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="10">
						<el-form-item prop="consoleIp" :label="lang.base.consoleIp">
							<pana-ip-input v-model="form.consoleIp"></pana-ip-input>
						</el-form-item>
					</el-col>
					<el-col :span="4">
						<el-button type="primary" round size="small" @click="checkIp">{{lang.base.checkOccupancyGateway}}</el-button>
					</el-col>
				</el-row>
			</div>
		</el-form>
		<!--链接正确的提示框-->
		<el-dialog :title="lang.common.alert" :visible.sync="centerDialogVisible" width="30%" center>
			<div class="alert-content">
				<p>{{lang.base.connectSuccess}}</p>
				<span><i class="el-icon-circle-check"></i></span>
			</div>
			<span slot="footer" class="dialog-footer">
		    <!--<el-button @click="showNetworkClick">{{lang.common.cancle}}</el-button>-->
		    <el-button type="primary" @click="showNetworkClick">{{lang.common.confirm}}</el-button>
		  </span>
		</el-dialog>
		<!--密码或者用户名错误-->
		<el-dialog :title="lang.common.alert" :visible.sync="errorDialogVisible" width="30%" center>
			<div class="alert-content">
				<p>{{lang.base.errorAlert}}</p>
				<span><i class="el-icon-warning"></i></span>
			</div>
			<span slot="footer" class="dialog-footer">
		    <el-button @click="errorClick">{{lang.common.confirm}}</el-button>
		    <!--<el-button type="primary" @click="centerDialogVisible = false">{{lang.common.confirm}}</el-button>-->
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import ipInput from '@/components/ipInput'
	export default {
		components: {
			'pana-ip-input': ipInput
		},
		data: function() {
			return {
				form: {
					username: '',
					password: '',
					consoleIp: ''
				},
				rules: {
					username: [{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}],
					consoleIp: [{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}]
				},
				showNetwork: false,
				centerDialogVisible: false,
				errorDialogVisible: false,
				publicNetwork: [],
				subnetName: [],
				subnetList: [],
				pubnetIndex: 0,
				sunetIndex: 0,
				networkId: '',
				segment: '',
			}
		},
		methods: {
			//			验证账号
			verifyPw() {
				let self = this
				this.$refs.form.validate((valid) => {
					if(valid) {
						this.$http({
							url: this.api.post_account,
							method: 'post',
							data: {
								username: this.form.username,
								password: this.form.password
							}
						}).then(function(res) {
							if(res.data.code === 0) {
								self.centerDialogVisible = true
							} else if(res.data.code === 1) {
								self.errorDialogVisible = true
							}
						}, function(res) {

						});
					}
				})
			},
			showNetworkClick() {
				this.centerDialogVisible = false
				this.showNetwork = true
				this.getPublicNetwork()
			},
			errorClick() {
				this.errorDialogVisible = false
			},
			getPublicNetwork() {
				let self = this
				this.$http({
					url: this.api.get_panacube,
					method: 'get',
				}).then(function(res) {
					self.publicNetwork = res.data.data
					if(self.publicNetwork.length > 0) {
						self.pubnetIndex = 0
						self.changePub()
					}
				}, function(res) {

				});
			},
			//			改变公网
			changePub() {
				let subnetData = this.publicNetwork[this.pubnetIndex]
				this.subnetName = subnetData.subnets
				if(this.subnetName.length > 0) {
					this.sunetIndex = 0
					this.changeSubnet()
				}
			},
			//			改变子网
			changeSubnet() {
				let subnetSegment = this.subnetName[this.sunetIndex];
				this.networkId = subnetSegment.id;
				let name = subnetSegment.name,
					pools = subnetSegment.allocationPools;
				for(let i = 0; i < pools.length; i++) {
					pools[i].name = pools[i].start + " ~ " + pools[i].end;
				}
				this.subnetList = pools;
				if(this.subnetList.length > 0) {
					this.segment = 0
				}
			},
			//			检测ip是否被占用
			checkIp() {
				let self = this
				this.$refs.form.validate((valid) => {
					if(valid) {
						this.$http({
							url: this.api.get_verify_ip + this.common.formatGetUrl({
								ip: this.form.consoleIp
							}),
							method: 'get',
						}).then(function(res) {
							if(res.data.code === 0) {
								sessionStorage.setItem('cloudInfo', JSON.stringify(self.form))
								if(res.data.data === 'null') {
									self.$notify({
										title: self.lang.common.error,
										message: self.lang.common.ipOccupancy,
										type: 'error'
									});
								} else {
									self.$notify({
										title: self.lang.common.success,
										message: self.lang.common.ipSuccess,
										type: 'success'
									});
								}
							}
						}, function(res) {

						});
					}
				})
			}
		},
		mounted() {

		}
	}
</script>

<style scoped="scoped">
	.cloudDiv {
		width: 1000px;
		margin: 90px auto 0;
	}
	
	.alert-content {
		text-align: center;
		font-size: 16px;
	}
	
	.alert-content .el-icon-circle-check {
		color: #4fbead;
		font-size: 50px;
	}
	
	.alert-content .el-icon-warning {
		color: red;
		font-size: 50px;
	}
	
	.cloudDiv .pana-ip-input {
		min-width: 248px;
	}
</style>


