<template>
	<div class="upgrade-div">
		<div class="left">
			<ul class="upgrad-btn">
				<li :class="selectIndex===1?'active':''" @click="upgradBtn(1)"></li>
				<li :class="selectIndex===2?'active':''" @click="upgradBtn(2)"></li>
			</ul>
		</div>
		<div class="right">
			<h5>{{lang.base.upgradeSelect}}</h5>
			<div class="checkbox-div">
				<el-checkbox-group v-model="checkUpdateList" @change="changeCheckBox">
					<el-checkbox :label="key" v-for="(item,key) in lang.base.upgradList" style="line-height: 50px;">
						<div class="checkbox-desc">
							<div class="lefts">
								<img :src="item.imgurl" />
							</div>
							<div class="rights">
								<h6>{{item.name}}</h6>
								<span>{{item.desc}}</span>
							</div>
						</div>
					</el-checkbox>
				</el-checkbox-group>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				checkUpdateList: [],
				selectIndex: 1
			}
		},
		methods: {
			upgradBtn(index) {
				this.selectIndex = index
			},
			changeCheckBox(){
				console.log(this.checkUpdateList)
			}
		}
	}
</script>

<style scoped="scoped">
	.upgrade-div {
		width: 1010px;
		margin: 90px auto 0;
		overflow: hidden;
	}
	
	.upgrade-div .left {
		width: 358px;
		height: 400px;
		float: left;
		border-right: 1px solid #e0e0e0;
	}
	
	.upgrade-div .right {
		float: left;
		margin-left: 45px;
	}
	
	.checkbox-div {
		width: 600px;
		margin-top: 42px;
	}
	
	.checkbox-desc {
		height: 100px;
		margin-left: 10px;
		overflow: hidden;
	}
	
	.checkbox-desc .lefts {
		float: left;
	}
	
	.checkbox-desc .lefts img {
		width: 40px;
		margin-top: 10px;
	}
	
	.checkbox-desc .rights {
		float: left;
		width: 470px;
		margin-left: 20px;
	}
	
	.checkbox-desc .rights h6 {
		font-size: 16px;
		color: #666;
	}
	
	.checkbox-desc .rights p {
		width: 470px;
		word-break: break-word;
	}
	
	.upgrade-div .el-checkbox__input {
		margin-top: -80px;
	}
	
	.upgrad-btn {
		list-style-type: none;
		margin-top: 74px;
	}
	
	.upgrad-btn li {
		cursor: pointer;
	}
	
	.upgrad-btn li:nth-child(1) {
		width: 253px;
		height: 57px;
		background: url(/static/image/section-upgrad3.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.upgrad-btn li:nth-child(2) {
		width: 253px;
		height: 57px;
		background: url(/static/image/section-upgrad2.png) no-repeat;
		background-size: 100% 100%;
		margin-top: 48px;
	}
	
	.upgrad-btn li:nth-child(1).active {
		background: url(/static/image/section-upgrad1.png) no-repeat;
	}
	
	.upgrad-btn li:nth-child(2).active {
		background: url(/static/image/section-upgrad4.png) no-repeat;
	}
</style>
<style type="text/css">
	.upgrade-div .el-checkbox__label {
		vertical-align: top;
	}
	
	.upgrade-div .el-checkbox {
		white-space: normal;
	}
</style>


