<template>
	<div id="bg" @click="$router.push({name:'baseInfo'})"></div>
</template>

<script>
	export default {
		data: function() {
			return {
				time: 0
			}
		},
		methods: {
			countDown() {
				let self = this
				self.time--
			}
		},
		mounted() {
			let self = this;
			self.time = 5;
			self.setIntervaltime = setInterval(this.countDown, 1000);
		},
		watch: {
			'time': function(newVal, oldVal) {
				if(newVal == 0) {
					this.$router.push({
						name:'baseInfo'
					});
					clearInterval(this.setIntervaltime)
				}
			}
		}
	}
</script>

<style scoped="scoped">
	#bg {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		min-width: 100%;
		min-height: 100%;
		background: url(/static/image/index_bg.png) no-repeat;
		background-size: 100% 100%;
		cursor: pointer;
	}
</style>


