<template>
	<div class="cloudDiv">
		<el-form ref="form" :model="form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item :label="lang.base.account" prop="account">
						<el-input v-model="form.account" :placeholder="lang.base.accountPlaceholder"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="10">
					<el-form-item :label="lang.base.password" prop="password">
						<el-input type="password" v-model="form.password" :placeholder="lang.base.passwordPlaceholder"></el-input>
					</el-form-item>
				</el-col>
				<el-button type="primary" round size="small" style="margin-left: 10px;">{{lang.base.check}}</el-button>
			</el-row>
			<div>
			<!--<div v-show="showNetwork">-->
				<el-row>
					<el-col :span="12">
						<el-form-item :label="lang.base.consoleIp">
							<el-input></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</div>
		</el-form>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				form: {
					account: '',
					password: ''
				},
				rules: {
					account:[{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}],
					password:[{
						required: true,
						message: this.lang.common.required,
						trigger: 'blur'
					}]
				},
				showNetwork: false
			}
		},
		methods: {

		},
		mounted() {

		}

	}
</script>

<style scoped="scoped">
	.cloudDiv {
		width: 1000px;
		margin: 90px auto 0;
	}
</style>


