import Vue from 'vue'
import Router from 'vue-router'
import main from '../views/main.vue'
import index from '../views/index.vue'
import baseInfo from '../views/container/base.vue'
//集群
Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		name: 'main',
		component: main,
		redirect: 'index',
		children: [{
			path: 'index',
			name: 'index',
			component: index,
		}, {
			path:'baseInfo',
			name:'baseInfo',
			component:baseInfo
		}]
	}]
})


