<template>
    <el-row class="pana-ip-input">
        <el-col :span="6">
            <div class="el-input">
                <input class="el-input__inner" v-model="res[0]" :disabled="disable.indexOf(0) > -1" type="text" min="1" max="255" :placeholder="placeholder[0]" @focus="current = 0" @keydown="type" @keyup="combinateIp">.
            </div>
        </el-col>
        <el-col :span="6">
            <div class="el-input">
                <input class="el-input__inner" v-model="res[1]" :disabled="disable.indexOf(1) > -1" type="text" min="1" max="255" :placeholder="placeholder[1]" @focus="current = 1" @keydown="type" @keyup="combinateIp" style="ime-mode:disabled">.
            </div>
        </el-col>
        <el-col :span="6">
            <div class="el-input">
                <input class="el-input__inner" v-model="res[2]" :disabled="disable.indexOf(2) > -1" type="text" min="1" max="255" :placeholder="placeholder[2]" @focus="current = 2" @keydown="type" @keyup="combinateIp" style="ime-mode:disabled">.
            </div>
        </el-col>
        <el-col :span="6">
            <div class="el-input">
                <input class="el-input__inner" v-model="res[3]" :disabled="disable.indexOf(3) > -1" type="text" min="1" max="255" :placeholder="placeholder[3]" @focus="current = 3" @keydown="type" @keyup="combinateIp" style="ime-mode:disabled">
            </div>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "pana-ip-input",
        methods: {
            type: function (e) {
                let index = this.current;
                if(e.target.value.toString().length === 3 && isNaN(Number(e.key)) === false) {
                    e.returnValue = false;
                }
                if(isNaN(Number(e.key))){
                    if(e.key === '.' && this.res[index] != null){
                        if(index < 3){
                            this.nextInput(e.target);
                            e.returnValue = false;
                        }
                        else{
                            e.target.blur();
                        }
                    }
                    if(e.key === 'Backspace' && index > 0 && (this.res[index] == null || this.res[index] === '')) {
                        e.target.parentNode.parentNode.previousElementSibling.childNodes[0].childNodes[0].focus();
                    }
                    if(this.ignoreKey.indexOf(e.key) === -1){
                        e.returnValue = false;
                    }
                }
            },
            nextInput: function (target) {
                let next = target.parentNode.parentNode.nextElementSibling.childNodes[0].childNodes[0],
                    keyDown = new Event('keydown');
                next.focus();
                next.dispatchEvent(keyDown);
            },
            combinateIp: function(e){
                e.target.value = e.target.value.replace(/\D/g, '');
                if(parseInt(e.target.value) > 255) {
                    e.target.value = 255;
                    this.$message({
                        message: this.lang.common.warningTooLargeIpNum,
                        type: 'error',
                        center: true
                    });
                }
                this.resValue = this.res.map(item => {
                    item = item.replace(/\D/g, '');
                    return item > 255 ? 255 : item;
                }).join('.');
                this.$emit('input', this.resValue == '...' ? '' : this.resValue);
            }
        },
        data: function () {
            return {
                current: 0,
                resValue: '',
                ignoreKey: ["Backspace", "ArrowLeft", "ArrowRight", "Delete", "Tab"],
            };
        },
        computed:{
            res: function () {
                let value = this.value || '';
                return value.split(".");
            }
        },
        props: {
            value: {
                type: String,
                default: ''
            },
            disable: {
                type: Array,
                default: ()=>{return []}
            },
            placeholder: {
                type: Array,
                default: ()=>{return []},
            }
        }
    }
</script>

<style scoped>
    .el-input{
    }
    .pana-ip-input{
        border: 1px solid #dcdfe6;
        border-radius: 4px;
        min-width: 300px;
    }
</style>
<style>
    .pana-ip-input .el-input__inner{
        border: 0!important;
        width: 95%!important;
        ime-mode:disabled;
    }
    .pana-ip-input .el-input__inner:hover{
        border: none!important;
    }
</style>



